import React from 'react'
import {useSpring, animated} from 'react-spring'
import SplitText from './splitText'

const HeroSection = ({setLoadState, loadState}) => {

    const header = "I'm Prateek, UI/UX Designer and a Full-Stack Web Developer. \nSpecialized on creating amazing experiences\n, on web and mobile devices.".split("\n")

    const Shift = useSpring({
        to: {
            marginTop: 0
        }, 
        from: {
            marginTop: window.innerHeight/4
        }, 
        delay: 6000,
        config: {
            duration: 600
        }
    })

    return (
        <div className="hero">
            <animated.h1 className="hero-header" style={Shift}>
                <SplitText>
                    {header}
                </SplitText>
            </animated.h1>
        </div>
    )
}

export default HeroSection